body {
  margin: 0;
  padding: 0;
  background-color: #eee;
}
*{
  box-sizing: border-box;
}
.contact {
  width: 100%;
  padding-top: 8rem;
  padding-bottom: 8rem;
}
.contactForm {
  display: block;
  background-color: #fff;
  box-shadow: 1px 1px 3px 2px #ddd;
  margin: 0px auto;
  max-width: 600px;
  max-height: 700px;
  padding: 25px 40px;
}
.contactForm input, textarea {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 10px auto;
  padding: 8px;
  background-color: #F1F1F1;
  border: 1px solid #ddd;
}
.contactForm span {
  color: rgba(29, 185, 237);
}
.contactForm button {
  height: 30px;
  width: 80px;
  align-items: center;
  border: none;
  background-color: rgba(29, 185, 237);
  color: #fff;
  cursor: pointer;
}