.portfolio .container {
    width: 70%;
    margin: 0 auto;
  }
  .portfolio .content h1 {
    font-size: 35px;
    font-weight: 500;
  }
  .portfolio .content {
    display: flex;
    flex-direction: column;
    margin-bottom: 5rem;
  }
   .portfolio .content div {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 5px;
    box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0);
    transition: 1s;
    cursor: pointer;
  }

  
  .portfolio .content .p1:hover {
   transform:scale(1.3);
   z-index: 10;
  }
  .portfolio .content .p2:hover {
    transform:scale(1.3);
    z-index: 10;
   }